// Date형식을 MMDD hhmmss 형식으로 변환하는 함수
export function dateToString(date: Date) {
  const month = String(date.getMonth() + 1).padStart(2, '0'); // 월
  const day = String(date.getDate()).padStart(2, '0'); // 일
  const hours = String(date.getHours()).padStart(2, '0'); // 시
  const minutes = String(date.getMinutes()).padStart(2, '0'); // 분
  const seconds = String(date.getSeconds()).padStart(2, '0'); // 초
  return `${month}${day} ${hours}${minutes}${seconds}`;
}

// MMDD hhmmss 형식을 yyyy-MM-dd'T'HH:mm:ss.SSS 형식으로 변환하는 함수
export function stringToDate(time: string): Date | null {
  const timeRegex = /^\d{4} \d{6}$/; // MMDD hhmmss 형식 체크
  if (!timeRegex.test(time)) {
    return null; // 유효하지 않은 형식은 null 반환
  }

  const month = parseInt(time.slice(0, 2), 10) - 1;
  const day = parseInt(time.slice(2, 4), 10);
  const hours = parseInt(time.slice(5, 7), 10);
  const minutes = parseInt(time.slice(7, 9), 10);
  const seconds = parseInt(time.slice(9, 11), 10);

  // 유효한 날짜인지 추가 검증
  const date = new Date(new Date().getFullYear(), month, day, hours, minutes, seconds);
  if (date.getMonth() !== month || date.getDate() !== day) {
    return null; // 날짜가 유효하지 않으면 null 반환
  }

  return date;
}

/**
 * Date 객체를 yyyy-MM-dd'T'HH:mm:ss.SSS 형식의 문자열로 변환하는 함수
 * @param date 변환할 Date 객체
 * @returns yyyy-MM-dd'T'HH:mm:ss.SSS 형식의 문자열
 */
export function formatDateToISOStringWithoutZ(date: Date): string {
  const year = date.getFullYear();
  const month = String(date.getMonth() + 1).padStart(2, '0'); // 월 (0부터 시작하므로 +1 필요)
  const day = String(date.getDate()).padStart(2, '0'); // 일
  const hours = String(date.getHours()).padStart(2, '0'); // 시
  const minutes = String(date.getMinutes()).padStart(2, '0'); // 분
  const seconds = String(date.getSeconds()).padStart(2, '0'); // 초
  const milliseconds = String(date.getMilliseconds()).padStart(3, '0'); // 밀리초

  return `${year}-${month}-${day}T${hours}:${minutes}:${seconds}.${milliseconds}`;
}

// Milliseconds 를 hh:mm:ss 형식의 문자열로 변환합니다.
export function formatTimeToHHMMSS(timeInMs: number): string {
  const hours = Math.floor(timeInMs / (1000 * 60 * 60));
  const minutes = Math.floor((timeInMs % (1000 * 60 * 60)) / (1000 * 60));
  const seconds = Math.floor((timeInMs % (1000 * 60)) / 1000);

  return `${String(hours).padStart(2, '0')}:${String(minutes).padStart(2, '0')}:${String(seconds).padStart(2, '0')}`;
}
